<template>
  <v-container
    fluid
    fill-height
  >
    <v-layout
      row
      wrap
    >
      <v-flex
        xs6
        sm6
        md9
        lg9
        class="text-xs-center mt-3 font-weight-bold title"
      >{{month }} - {{year}}</v-flex>
      <v-flex
        xs5
        sm5
        md2
        lg2
      >
        <v-menu
          ref="menu"
          :close-on-content-click="false"
          v-model="menu"
          :nudge-right="40"
          :return-value.sync="date"
          lazy
          transition="scale-transition"
          offset-y
          full-width
          max-width="290px"
          min-width="290px"
        >
          <v-text-field
            slot="activator"
            v-model="date"
            label="Select month"
            prepend-icon="event"
            readonly
          ></v-text-field>
          <v-date-picker
            color="primary"
            v-model="date"
            type="month"
            no-title
            scrollable
            min="2018-04"
            :max="maxAllowedMonth()"
          >
            <v-spacer></v-spacer>
            <v-btn
              flat
              color="primary"
              @click="menu = false"
            >Cancel</v-btn>
            <v-btn
              flat
              color="primary"
              @click="$refs.menu.save(date);getChartData(date)"
            >OK</v-btn>
          </v-date-picker>
        </v-menu>
      </v-flex>
      <loading
        :active.sync="isLoading"
        :is-full-page="fullPage"
        color="#7f0000"
        loader="dots"
        :width='width'
        :height='height'
      ></loading>
      <v-flex
        md2
        lg2
      ></v-flex>
      <v-flex
        xs12
        sm12
        md4
        lg4
        class="mb-5"
      >
        <highcharts :options="chartOptions5"></highcharts>
      </v-flex>
      <v-flex
        md1
        lg1
      ></v-flex>
      <v-flex
        xs12
        sm12
        md4
        lg4
        class="mb-5"
      >
        <highcharts :options="chartOptions"></highcharts>
      </v-flex>
      <v-flex
        md1
        lg1
      ></v-flex>
      <v-flex
        xs12
        sm12
        md3
        lg3
        class="mb-5"
      >
        <highcharts :options="chartOptions1"></highcharts>
      </v-flex>
      <v-flex
        xs12
        sm12
        md3
        lg3
        class="mb-5"
      >
        <highcharts :options="chartOptions2"></highcharts>
      </v-flex>
      <v-flex
        xs12
        sm12
        md3
        lg3
        class="mb-5"
      >
        <highcharts :options="chartOptions3"></highcharts>
      </v-flex>
      <v-flex
        xs12
        sm12
        md3
        lg3
        class="mb-5"
      >
        <highcharts :options="chartOptions4"></highcharts>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { serverBus } from "../main";
import moment from "moment";
export default {
  data: () => ({
    date: null, //
    month: null,
    year: null,
    width: 30,
    height: 30,
    menu: false,
    chartOptions: {
      chart: {
        type: 'column',
        backgroundColor: '#FAFAFA',
        height: 250,
        // width: 400,
        animation: {
          duration: 1000,
          reflow: true
        }
      },
      tooltip: {
        formatter: function () {
          return this.x + ':' + this.y;
        }
      },
      title: {
        text: 'Top 5 Revenue ( lacs)',
        style: {
          color: '#000000',
          font: 'bold 20px "Trebuchet MS", Verdana, sans-serif'
        }
      },
      loading: {
        hideDuration: 1000,
        showDuration: 1000
      },
      xAxis: {
        categories: null,
        labels: {
          style: {
            color: '#424242',
            fontSize: '13px'
          }
        }
      },
      yAxis: {
        title: {
          text: '% contrib',
          enabled: false
        },
        gridLineWidth: 0.5,
        minorGridLineWidth: 0
      },
      credits: {
        enabled: false
      },
      plotOptions: {
        column: {
          pointPadding: 0.3,
          borderWidth: 1,
          groupPadding: 0,
          // pointWidth: 35,
          dataLabels: {
            enabled: true
          }
        }
      },
      series: [{
        showInLegend: false,
        data: null,
        // pointWidth: 30,
        animation: false
      }]
    },
    chartOptions1: {
      chart: {
        type: 'column',
        backgroundColor: '#FAFAFA',
        height: 250,
        // width: 400,
        animation: {
          duration: 1000,
          reflow: true
        }
      },
      tooltip: {
        formatter: function () {
          return this.x + ':' + this.y;
        }
      },
      title: {
        text: 'Top 5 Opticals ( lacs)',
        style: {
          color: '#000000',
          font: 'bold 20px "Trebuchet MS", Verdana, sans-serif'
        }
      },
      credits: {
        enabled: false
      },
      xAxis: {
        categories: null,
        labels: {
          style: {
            color: '#424242',
            fontSize: '13px'
          }
        }
      },
      yAxis: {
        title: {
          text: '% contrib',
          enabled: false
        },
        gridLineWidth: 0.5,
        minorGridLineWidth: 0
      },
      plotOptions: {
        column: {
          pointPadding: 0.3,
          borderWidth: 1,
          groupPadding: 0,
          // pointWidth: 35,
          dataLabels: {
            enabled: true
          }
        }
      },
      series: [{
        showInLegend: false,
        data: null,
        // pointWidth: 30,
        animation: false
      }]
    },
    chartOptions2: {
      chart: {
        type: 'column',
        backgroundColor: '#FAFAFA',
        height: 250,
        // width: 400,
        animation: {
          duration: 1000,
          reflow: true
        }
      },
      tooltip: {
        formatter: function () {
          return this.x + ':' + this.y;
        }
      },
      credits: {
        enabled: false
      },
      title: {
        text: 'Top 5 Pharmacy ( lacs)',
        style: {
          color: '#000000',
          font: 'bold 20px "Trebuchet MS", Verdana, sans-serif'
        }
      },
      xAxis: {
        categories: null,
        labels: {
          style: {
            color: '#424242',
            fontSize: '13px'
          }
        }
      },
      yAxis: {
        title: {
          text: '% contrib',
          enabled: false
        },
        gridLineWidth: 0.5,
        minorGridLineWidth: 0
      },
      plotOptions: {
        column: {
          pointPadding: 0.3,
          borderWidth: 1,
          groupPadding: 0,
          // pointWidth: 35,
          dataLabels: {
            enabled: true
          }
        }
      },
      series: [{
        showInLegend: false,
        data: null,
        // pointWidth: 30,
        animation: false
      }]
    },
    chartOptions3: {
      chart: {
        type: 'column',
        backgroundColor: '#FAFAFA',
        height: 250,
        // width: 400,
        animation: {
          duration: 1000,
          reflow: true
        }
      },
      tooltip: {
        formatter: function () {
          return this.x + ':' + this.y;
        }
      },
      credits: {
        enabled: false
      },
      title: {
        text: 'Top 5 Surgery ( lacs)',
        style: {
          color: '#000000',
          font: 'bold 20px "Trebuchet MS", Verdana, sans-serif'
        }
      },
      xAxis: {
        categories: null,
        labels: {
          style: {
            color: '#424242',
            fontSize: '13px'
          }
        }
      },
      yAxis: {
        title: {
          text: '% contrib',
          enabled: false
        },
        gridLineWidth: 0.5,
        minorGridLineWidth: 0
      },
      plotOptions: {
        column: {
          pointPadding: 0.3,
          borderWidth: 1,
          groupPadding: 0,
          // pointWidth: 35,
          dataLabels: {
            enabled: true
          }
        }
      },
      series: [{
        showInLegend: false,
        data: null,
        // pointWidth: 30,
        // groupPadding: 0,
        animation: false
      }]
    },
    chartOptions4: {
      chart: {
        type: 'column',
        backgroundColor: '#FAFAFA',
        height: 250,
        // width: 400,
        animation: {
          duration: 1000,
          reflow: true
        }
      },
      tooltip: {
        formatter: function () {
          return this.x + ':' + this.y;
        }
      },
      credits: {
        enabled: false
      },
      title: {
        text: 'Top 5 Op (count)',
        style: {
          color: '#000000',
          font: 'bold 20px "Trebuchet MS", Verdana, sans-serif'
        }
      },
      xAxis: {
        categories: null,
        labels: {
          style: {
            color: '#424242',
            fontSize: '13px'
          }
        }
      },
      yAxis: {
        title: {
          text: '% contrib',
          enabled: false
        },
        gridLineWidth: 0.5,
        minorGridLineWidth: 0
      },
      plotOptions: {
        column: {
          pointPadding: 0.3,
          borderWidth: 1,
          groupPadding: 0,
          // pointWidth: 35,
          dataLabels: {
            enabled: true
          }
        }
      },
      series: [{
        showInLegend: false,
        data: null,
        // pointWidth: 30,
        // groupPadding: 0,
        animation: false
      }]
    },
    chartOptions5: {
      chart: {
        backgroundColor: '#FAFAFA',
        height: 250,
        // width: 400,
        type: 'column',
        // animation: {
        //   duration: 1000,
        //   reflow: true
        // }
      },
      tooltip: {
        shared: true,
        // formatter: function () {
        //   return this.x[1] + ':' + this.y[1];
        // }
      },
      credits: {
        enabled: false
      },
      title: {
        text: 'Top 5 Profitable ( lacs)',
        style: {
          color: '#000000',
          font: 'bold 20px "Trebuchet MS", Verdana, sans-serif'
        }
      },
      xAxis: {
        categories: null,
        labels: {
          style: {
            color: '#424242',
            fontSize: '13px'
          }
        }
      },
      legend: {
        layout: 'vertical',
        align: 'left',
        x: 120,
        verticalAlign: 'top',
        y: 100,
        floating: true
      },
      yAxis: {
        title: {
          text: '% contrib',
          enabled: false
        },
        gridLineWidth: 0.5,
        minorGridLineWidth: 0
      },
      plotOptions: {
        column: {
          grouping: false,
          shadow: false,
          borderWidth: 0,
          dataLabels: {
            enabled: true

          }
        }
      },
      legend: {
        shadow: false,
        layout: 'horizontal',
        verticalAlign: 'top',
        floating: false,
      },
      series: [{
        dataLabels: [{
          // align: 'top',
          format: '{point.t}%',
          crop: false,
          overflow: 'none'
        }],
        name: 'actual',
        data: null,
        color: 'rgba(165,170,217,1)',
        // pointPadding: 0.2,
        // pointPlacement: -0.2,
        // pointWidth: 30,
        // groupPadding: 0,
        animation: false
      },
      {
        dataLabels: [{
          // align: 'top',
          format: '',
          crop: false,
          overflow: 'none'
        }],
        name: 'target',
        color: 'rgb(0,0,0,0.5)',
        data: null,
        // pointPadding: 0.1,
        // pointPlacement: -0.2,
        // pointWidth: 30,
        // groupPadding: 0,
        animation: false,
      }]
    },
    isLoading: false,
    isLoading1: true,
    isLoading2: true,
    isLoading3: true,
    fullPage: false,
    fullPage1: false,
    fullPage2: false,
    fullPage3: false
  }),
  created () {
    this.getChartData()
    this.dispDate()
  },
  methods: {
    getChartData (date) {
      if (date === undefined) {
        this.isLoading = true
        date = this.maxAllowedMonth()
        this.axios.get(`https://scm.dragarwal.com/chart/${date}`).then(response => {
          this.processChart(response.data)
          this.getMonth()
          this.isLoading = false
        })
      }
      else {
        this.isLoading = true
        this.axios.get(`https://scm.dragarwal.com/chart/${date}`).then(response => {
          this.processChart(response.data)
          this.getMonth()
          this.isLoading = false
        })
      }
    },
    maxAllowedMonth () {
      let date = new Date();
      return date.getFullYear() + '-' + (date.getMonth())
    },
    processChart (response) {
      let top5branches = [], top5branchesmtd = [], top5opticalsbranches = [], top5opticalsrev = [], top5pharmacybranches = [], top5pharmacyrev = [], top5surgerybranches = [], top5surgeryrev = [], top5oprev = [], top5opbranches = [], top5targetbranches = [], top5targetrev = [], top5targetspline = []
      response.top5.forEach(element => {
        top5branches.push(element.native)
        top5branchesmtd.push({ y: Number((element.mtd / 100000).toFixed(2)), color: '#EF6C00' })
      });
      this.chartOptions.xAxis.categories = top5branches
      this.chartOptions.series[0].data = top5branchesmtd
      response.top5opticals.forEach(element => {
        top5opticalsbranches.push(element.native)
        top5opticalsrev.push({ y: Number((element.opticals / 100000).toFixed(2)), color: '#343977' })
      });
      this.chartOptions1.xAxis.categories = top5opticalsbranches
      this.chartOptions1.series[0].data = top5opticalsrev
      response.top5pharma.forEach(element => {
        top5pharmacybranches.push(element.native)
        top5pharmacyrev.push({ y: Number((element.pharmacy / 100000).toFixed(2)), color: '#ab1234' })
      });
      this.chartOptions2.xAxis.categories = top5pharmacybranches
      this.chartOptions2.series[0].data = top5pharmacyrev
      response.top5surgery.forEach(element => {
        top5surgerybranches.push(element.native)
        top5surgeryrev.push({ y: Number((element.surgery / 100000).toFixed(2)), color: '#ll0045' })
      });
      this.chartOptions3.xAxis.categories = top5surgerybranches
      this.chartOptions3.series[0].data = top5surgeryrev
      response.top5op.forEach(element => {
        top5opbranches.push(element.branch)
        top5oprev.push({ y: Number(element.sum_count), color: '#445100' })
      });
      this.chartOptions4.xAxis.categories = top5opbranches
      this.chartOptions4.series[0].data = top5oprev
      response.top5target.forEach(element => {
        top5targetbranches.push(element.code)
        top5targetrev.push({ y: Number((element.bar / 100000).toFixed(2)), t: Number((element.target - 100).toFixed(2)) })
        top5targetspline.push({ y: Number(element.spline) })
      });
      this.chartOptions5.xAxis.categories = top5targetbranches
      this.chartOptions5.series[0].data = top5targetrev
      this.chartOptions5.series[1].data = top5targetspline
    },
    getMonth () {
      const monthNames = ["January", "February", "March", "April", "May", "June",
        "July", "August", "September", "October", "November", "December"
      ];
      this.month = monthNames[new Date(this.date).getMonth()]
      this.year = new Date(this.date).getFullYear()
    },
    dispDate () {
      this.date = moment().subtract(1, 'months').format('YYYY-MM')
    }
  }
};
</script>

<style>
</style>



