<template>
  <v-container
    fluid
    fill-height
  >
    <v-layout
      align-center
      justify-center
    >
      <v-flex
        xs12
        sm8
        md5
      >
        <v-card
          class="elevation-12"
          tile
          hover
        >
          <v-toolbar
            dark
            color="grey darken-2"
          >
            <v-toolbar-title>Sign In</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <v-card-text>
            <v-form
              ref="form"
              v-model="valid"
              lazy-validation
            >
              <v-text-field
                v-model="name"
                :rules="nameRules"
                label="Name"
                required
                @keydown.enter="submit"
                autofocus="autofocus"
              ></v-text-field>
              <v-text-field
                v-model="password"
                :rules="passRules"
                label="Password"
                required
                @keydown.enter="submit"
                :append-icon="show1 ? 'visibility' : 'visibility_off'"
                @click:append="show1 = !show1"
                :type="show1 ? 'text' : 'password'"
              ></v-text-field>
              <!-- <v-btn color=primary :disabled="!valid" @click="submit">
                                Login
              </v-btn>-->
              <v-spacer></v-spacer>
              <v-btn
                :disabled="!valid"
                color="grey darken-2"
                class="white--text"
                @click="submit"
                :loading="loading"
              >Login</v-btn>
              <span
                class="red--text font-weight-regular"
                :v-if="errors.length"
              >{{errors.toString()}}</span>
            </v-form>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { serverBus } from "../main";

export default {
  data: () => ({
    loading: false,
    valid: true,
    show1: false,
    name: "",
    nameRules: [v => !!v || "Name is required"],
    password: "",
    passRules: [v => !!v || "Password is required"],
    errors: [],
    userName: null
  }),  methods: {
    submit () {
      console.log(this.$refs.form.validate());
      if (this.$refs.form.validate())
      {
        this.loading = true;
        this.axios
          .post(`https://scm.dragarwal.com/login`, {

        //     .post(`http://localhost:7777/login`, {

            user: this.name.trim(),

            pass: this.password.trim(),

          })



          .then(response => {
            console.log(response);
            this.loading = false;
            if (
              response.data.isAuthenticated === true &&
              response.data.role === "super_user"
            ) {
              sessionStorage.setItem("super_user", JSON.stringify({ name: this.name, userName: response.data.userName }));
              serverBus.$emit("changeComponent", "revenueSuper");
              // serverBus.$emit("changeComponent", "chart");
            } else if (
              response.data.isAuthenticated === true &&
              response.data.role === "normal_user"
            ) {
              sessionStorage.setItem("normal_user", JSON.stringify({ name: this.name, userName: response.data.userName }));
              serverBus.$emit("changeComponent", "revenue");
              // serverBus.$emit("changeComponent", "chart");
            } else {
              this.errors = ["Invalid Creds"];
            }
          });
      }
    }

  }
};
</script>

<style>
</style>



